<template>
  <div class="g-details">
    <div class="m-swiper">
      <cube-slide :showDots="showDots" :data="items"/>
    </div>
    <div class="m-msg">
      <span class="s-title beyond">{{bit.proName}}</span>
      <div v-if="bit.sortId==1" class="s-money">
        <span class="total">
          积分：{{bit.price}}
        </span>
        <span class="pv">
          PV:{{bit.integral}}
        </span>
      </div>
      <div v-else class="s-money">
        <span class="total">
          元宝：{{bit.price}}
        </span>
      </div>
      <div class="s-ps">
        <span class="volume">
          销量：{{bit.saleVolume||0}}件
        </span>
        <!--<span class="money">
          运费：10元
        </span>-->
      </div>
    </div>
    <div class="m-border"></div>
    <router-link  v-if="bit.sortId==1" :to="{name:'classifyDetails',query:{titlename:shop.name,shopId:shop.id}}" class="m-shop" tag="div">
      <div class="s-img">
        <img :src="shop.icon" alt="">
      </div>
      <div class="s-name">{{shop.name}}</div>
      <div class="s-btn">
        进入店铺
        <i></i>
      </div>
    </router-link>
    <div class="m-border"></div>
    <div class="m-content">
      <div class="s-title">
        图文详情
      </div>
      <div class="s-content">
        <img v-for="(v,i) in pics" :key="i" v-lazy="'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+v" alt="">
      </div>
    </div>
    <buyfixed :member="shop.username" :proName="bit.proName" :price="bit.price" :sortId="bit.sortId" :pic="bit.carousels[0]"/>
  </div>
</template>
<script>
  import buyfixed from '@/components/buyfixed'
  export default {
    name: "detail",
    data(){
      return {
        showDots: false,
        bit:{
          carousels:[],
          proName:'',
          sortId:'',
        },
        shop:{
          username:'请联系APP客服'
        },
        items: [],//轮播图
        pics:[],//商品详情图
        shopName:''
      }
    },
    created(){
      this.details();//商品详情
    },
    methods:{
      details(){
        this.$post(this.$bee+'/api/product/productDetail',{id:this.$route.query.id}).then((data)=> {
          if (data.code == 200) {
            if(data.data.shop){
              this.shop=data.data.shop;
            }
            this.bit=data.data.pro;
            this.pics=data.data.pro.pics;
            data.data.pro.carousels.forEach((i) => {
              this.items.push({
                url: "javascript:void(0)",
                image:'https://fx18.oss-cn-hangzhou.aliyuncs.com/'+i
              })
            })
          } else {
            this.$createToast({
              txt: data.message,
              type: 'txt'
            }).show();
          }
        })
      }
    },
    components: {
      buyfixed
    },
    beforeRouteLeave(to, from, next) {
      if(to.name=='classifyDetails'){
        to.meta.title=this.shopName
      }
      next();
    }
  }
</script>

<style scoped lang="stylus">
  .g-details
    .m-swiper
      width 100%
      & >>> .cube-slide
        width 100%
        img
          width 100%
          height 100%
    .m-msg
      text-align left
      padding 15px 12.5px 9px
      .s-title
        color #444
        font-size 13.5px
        margin-bottom 5px
        padding 5px 0
        display block
      .s-money
        color #cc5b24
        font-size 14.5px
        margin-bottom 10.5px
        .pv
          margin-left 19px
      .s-ps
        color #b2b2b2
        font-size 12px
        .money
          margin-left 150px
    .m-border
      height 4px
      width 100%
      background-color #e3e3e3
    .m-shop
      display flex
      flex-wrap wrap
      line-height 40px
      padding 10px 12px
      position relative
      .s-img
        width 40px
        height 40px
        background-color #b77979
        border-radius 50%
        overflow hidden
        img
          width 100%
          height 100%
      .s-name
        color #323232
        font-size 15px
        margin-left 7px
      .s-btn
        color #9d9c9c
        font-size 13px
        position absolute
        right 14px
        vertical-align middle
        i
          display inline-block
          vertical-align middle
          width 7px
          height 12px
          margin-left 5px
          background url(../assets/images/right-arrow.png) no-repeat center center
          background-size 100% 100%
    .m-content
      padding 30px 0 50px 0
      .s-title
        color #323232
        font-size 15px
        position relative
        margin-bottom 30px
        &:after
          content ''
          display block
          position absolute
          width 55px
          height 2px
          top 25px
          left calc(50% - 27.5px)
          background #b2b2b2
      .s-content
        img
          width 100%
  .slide-fade-enter-active, .slide-fade-leave-active
    transition: all .5s
  .slide-fade-enter, .slide-fade-leave-to
    transform: translateY(100px);
    opacity: 0;
</style>
